@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    max-width: 1150px;
  }
}

.page {
  height: 100%;
  background-color: #ffffff;
}

.page__body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  margin: 0;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
  font-style: normal;
  word-break: break-word;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .page__body {
    grid-template-rows: 1fr auto;
  }
}
@media (min-width: 768px) {
  .page__body {
    font-size: 17px;
  }
}

@media (max-width: 767px) {
  .page__main {
    position: relative;
    left: 0;
    right: 0;
    top: 62px;
  }
}

.page-header {
  position: relative;
}

@media (max-width: 767px) {
  .page-header__main-nav {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 10;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .page-header__main-nav {
    display: grid;
    grid-template-columns: 370px 1fr;
    grid-template-rows: auto auto;
    align-items: center;
    margin: 0 auto;
  }
}
@media (min-width: 1150px) {
  .page-header__main-nav {
    grid-template-columns: 1fr 150px 1fr;
    column-gap: 40px;
    margin: 0;
    width: 100%;
  }
}

@media (max-width: 767px) {
  .page-header__main-nav--nojs {
    position: relative;
    margin: 0 0 -62px;
  }
}

@media (min-width: 768px) {
  .page-header__main-nav::before {
    content: "";
    display: block;
    height: 100%;
    grid-column: 1/-1;
    grid-row: 2/3;
    background-color: #f1f1f1;
  }
}

@media (min-width: 768px) {
  .page-header--alter-background .page-header__main-nav::before {
    background-color: #f9f9f9;
  }
}

@media (min-width: 768px) {
  .page-header__main-nav::after {
    content: "";
    position: relative;
    z-index: 11;
    align-self: start;
    height: 1px;
    grid-column: 1/-1;
    grid-row: 2/3;
    background-color: transparent;
    box-shadow: inset 0 1px 0 0 #ececec;
  }
}

@media (max-width: 767px) {
  .page-header__logo-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 80px;
    box-shadow: inset 0 -1px 0 0 #ececec;
  }
}
@media (min-width: 768px) {
  .page-header__logo-wrapper {
    grid-column: 1/2;
    grid-row: 1/2;
    padding: 22px 48px 20px 48px;
  }
}
@media (min-width: 1150px) {
  .page-header__logo-wrapper {
    grid-column: 2/3;
    grid-row: 1/2;
    padding: 9px 0 7px;
  }
}

.page-header__logo {
  display: block;
  width: 86px;
  height: 35px;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 114px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .page-header__logo {
    width: 149px;
    height: 72px;
  }
}
.page-header__logo:hover, .page-header__logo:focus {
  opacity: 0.6;
}
.page-header__logo:active {
  opacity: 0.3;
}

.page-header__toggle {
  position: absolute;
  right: 31px;
  top: 32px;
  padding: 0;
  display: block;
  width: 20px;
  height: 14px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

.page-header__main-nav--opened .page-header__toggle {
  right: 30px;
  top: 29px;
  width: 21px;
  height: 20px;
}

.page-header__main-nav--nojs .page-header__toggle {
  display: none;
}

.page-header__burger {
  display: block;
  width: 20px;
  height: 2px;
  background-color: #000000;
}

.page-header__main-nav--opened .page-header__burger {
  width: 25px;
  transform: translate(-2px, 0) rotate(-45deg);
}

.page-header__burger::before {
  content: "";
  display: block;
  width: 20px;
  height: 2px;
  background-color: #000000;
  transform: translateY(-6px);
}

.page-header__main-nav--opened .page-header__burger::before {
  width: 25px;
  transform: translate(0, 0) rotate(90deg);
}

.page-header__burger::after {
  content: "";
  display: block;
  width: 20px;
  height: 2px;
  background-color: #000000;
  transform: translateY(4px);
}

.page-header__main-nav--opened .page-header__burger::after {
  display: none;
}

.page-header__main-site-menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .page-header__main-site-menu {
    display: none;
  }
}
@media (min-width: 768px) {
  .page-header__main-site-menu {
    grid-column: 1/2;
    grid-row: 2/3;
    display: flex;
    flex-wrap: wrap;
    column-gap: 37px;
    margin: 0 0 0 50px;
  }
}
@media (min-width: 1150px) {
  .page-header__main-site-menu {
    grid-column: 1/2;
    grid-row: 1/2;
    column-gap: 0;
    margin: 0 48px 0 auto;
    max-width: 420px;
  }
}

.page-header__main-nav--opened .page-header__main-site-menu,
.page-header__main-nav--nojs .page-header__main-site-menu {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .page-header__main-nav--opened .page-header__main-site-menu,
.page-header__main-nav--nojs .page-header__main-site-menu {
    flex-direction: column;
    background-color: #ffffff;
  }
}

.page-header__main-site-menu-item {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .page-header__main-site-menu-item {
    box-shadow: inset 0 -1px 0 0 #ececec;
  }
}

.page-header__main-site-menu-link {
  transition: all 0.3s ease-in-out;
  margin: 0;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  color: #000000;
}
@media (max-width: 767px) {
  .page-header__main-site-menu-link {
    padding: 29px 30px 26px 76px;
    width: 100%;
  }
}
@media (min-width: 768px) and (max-width: 1149px) {
  .page-header__main-site-menu-link {
    margin: 0;
    font-size: 15px;
    line-height: 24px;
    font-weight: 700;
    color: #000000;
  }
}
@media (min-width: 768px) {
  .page-header__main-site-menu-link {
    position: relative;
    z-index: 11;
    padding: 22px 0;
  }
}
@media (min-width: 1150px) {
  .page-header__main-site-menu-link {
    padding: 34px 35px 30px 0;
  }
}
.page-header__main-site-menu-link:hover, .page-header__main-site-menu-link:focus {
  opacity: 0.6;
}
.page-header__main-site-menu-link:active {
  opacity: 0.3;
}

@media (min-width: 1150px) {
  .page-header__main-site-menu-item:nth-child(even) .page-header__main-site-menu-link {
    padding: 34px 35px 30px 35px;
    border-left: 1px solid #ececec;
  }
}

.page-header__user-menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .page-header__user-menu {
    display: none;
  }
}
@media (min-width: 768px) {
  .page-header__user-menu {
    grid-column: 2/-1;
    grid-row: 1/2;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin: 0 10px 0 auto;
    max-width: 400px;
  }
}
@media (min-width: 1150px) {
  .page-header__user-menu {
    grid-column: 3/4;
    justify-self: end;
    margin: 0 auto 0 72px;
  }
}

.page-header__main-nav--opened .page-header__user-menu,
.page-header__main-nav--nojs .page-header__user-menu {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .page-header__main-nav--opened .page-header__user-menu,
.page-header__main-nav--nojs .page-header__user-menu {
    flex-direction: column;
