    padding: 36px 32px 28px;
  }
}

.page-header__user-menu-link--full-text {
  padding: 28px 40px 27px 31px;
}
@media (min-width: 768px) {
  .page-header__user-menu-link--full-text {
    padding: 33px 40px 27px 31px;
  }
}
@media (min-width: 1150px) {
